import React from 'react';
import { Table, Space } from 'antd';
import styled from 'styled-components';

function TableComponent(props) 
{
    const { transformData } = props;
    
    const Icon = styled.img`
        min-height: 1rem;
        max-height: 2rem;
    `
  
    const IconOP = styled.img`
        min-height: 1rem;
        max-height: 2rem;
        border-radius: 100%;
    `
  
    const Pseudo = styled.h1`
        font-weight: bold;
        font-size: large;
    `
    
    const Struct = styled.span`
        color: #1b7eb7;
    `

    const columns = [
        {
          title : '#',
          dataIndex : 'rank',
          key: 'rank',
          sorter : (a,b) => a.hidenScore - b.hidenScore
        },
        {
          title: 'Pseudo',
          dataIndex : 'pseudo',
          key: 'pseudo',
          render : (text, record) =>
          <Pseudo><Struct>{record.structure} </Struct>{text}</Pseudo>,
          sorter : (a, b) => a.pseudo.localeCompare(b.pseudo),
        },
        
        {
          title: 'Rôle',
          dataIndex: 'role',
          key: 'role',
          render: (text) => (
            <Space size="middle">
              {text !== "" ? <IconOP src={text}/> : null}
            </Space>
          ),
        },
        {
          title: 'Rang',
          dataIndex : 'tier',
          key:'tier',
          render: (text, record) => (
            <Space size="middle">
              <Icon src={record.tierIcon}/>
              {text}
            </Space>
          ),
          sorter : (a,b) => a.hidenScore - b.hidenScore
        },
        {
          title: 'Winrate',
          dataIndex : 'winrate',
          key: 'winrate',
          render: text => (
            <span>{text} %</span>
          ),
          sorter : (a,b) => a.winrate - b.winrate
        },
        {
          title: 'Wins',
          dataIndex : 'wins',
          key: 'wins',
        },
        {
          title: 'Losses',
          dataIndex : 'losses',
          key: 'losses',
        },
        {
          title: 'Compte',
          dataIndex: 'summonerName',
          key: 'summonerName',
          render: (text) => (
            <Space size="middle">
              {text !== "" ? <a href={text} target="_blank" rel="noopener noreferrer"><IconOP src='./assets/OPGG.png'/></a> : null}
            </Space>
          ),
        },
        {
          title: 'Twitch',
          dataIndex: 'twitchLink',
          key: 'twitchLink',
          render: (text, record) => (
            <Space size="middle">
              {text !== "" ? <a href={text} target="_blank" rel="noopener noreferrer"><Icon src={record.isLive}/></a> : null}
            </Space>
          ),
        },
        {
          title: 'Twitter',
          dataIndex: 'xLink',
          key: 'xLink',
          render: (text) => (
            <Space size="middle">
              {text !== "" ? <a href={text} target="_blank" rel="noopener noreferrer"><Icon src='./assets/TWITTER.png'/></a> : null}
            </Space>
          ),
        }
    ]

  return (
    <Table dataSource={transformData} columns={columns} pagination={false} scroll={{ x : true }} />
  )
}

export default TableComponent