import React from 'react';
import Countdown from 'react-countdown';
import styled from 'styled-components';

function Header() 
{
    const targetDate = new Date('2024-01-23T23:59:59');
    
    const Header = styled.div`
        padding-left: 0rem;
        width: 100%;
        display: flex;
        flex-direction: row;
        gap : 0rem;
        align-items: center;
        justify-content: center;
        
        @media (max-width: 1280px) {
    /* Pour les écrans de 767 pixels de large ou moins (styles mobiles) */
            flex-direction: column;
        }
    `
      
    const HeaderContainer = styled.div`
      
        display: flex;
        flex:1;
        justify-content: center;
    `
  
    const Logo = styled.img `
        min-height: 22rem;
        max-height: 24rem;
    `
    
    const Count = styled.div`
        padding: 2rem;
        font-size: 3rem;
        font-family: 'zhcn', sans-serif;
    `
    
    const renderer = ({ total, days, hours, minutes, seconds }) => {
      if (total) {
        // Render a countdown
        return (
          <Count> 
            <span style={{ color: 'white' }}>
              {days}J {hours}H {minutes}min {seconds}s
            </span>
          </Count>
          
        );
      } else {
        // Render a finished state
        return null;
      }
    };
  
    return (
      <Header>
        <HeaderContainer>
          <Logo src='./assets/RUSH_S14_Titre.png'/>
        </HeaderContainer>
        <HeaderContainer>
          <Countdown date={targetDate} renderer={renderer}/>
        </HeaderContainer>
      </Header>
    )
}

export default Header;