import React , { useEffect , useState } from 'react';
import styled from 'styled-components';
import axios from 'axios';
import './App.css';
import Header from './components/Header';
import { getTransformPlayersData } from './utils/PlayerUtils';
import TableComponent from './components/TableComponent';
import simulateData from './simulateData.json';

function App() 
{
  let [ playersData , setPlayersData ] = useState([]);
  

  const Wrapper = styled.div`
    background-image: url('./assets/RUSH_S14_BG.png');
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    padding-bottom: 6rem;
    padding-left: 6rem;
    padding-right: 6rem;
    background-size: cover;
    background-position: center;
    background-attachment: fixed;
    overflow-y: hidden;
    
  `;
  
  const TabContainer = styled.div`
    margin : 2rem;
    height : fit-content;
    justify-content: center;
    align-items: center;
    display: flex;
    flex-direction: column;
  `
  
  async function getPlayersData()
  {
    //let response = await axios.get('http://localhost:2222/players/infos');
    let response = simulateData;
    setPlayersData(response);
  }
  
  useEffect(() => {
    getPlayersData();
  }, []);
  
  
  let transformData = getTransformPlayersData(playersData);
  
  setInterval(async () => {
    await getPlayersData();
  }, 120000);
  
  return (
    <Wrapper>
      <TabContainer>
        <Header />
        <TableComponent transformData={transformData} />
      </TabContainer>
    </Wrapper>
  );
}

export default App;