function getPlayerTier(player)
{
  let tier;
  
  if (player.gamesData === null)
  {
    return '';
  }
  
  if (player.gamesData.tier === "MASTER" || player.gamesData.tier === "GRANDMASTER" || player.gamesData.tier === "CHALLENGER")
  {
    tier = player.gamesData.tier + " " + player.gamesData.LeaguePoints + " lps"
  } else {
    tier = player.gamesData.tier + " " + player.gamesData.rank + " " + player.gamesData.LeaguePoints + " lps"
  }
  return tier;
}

function getPlayerSummonerName(player)
{
  return "https://www.op.gg/summoners/euw/" + player.summonerName + "-" + player.tag;
}

function getPlayerHidenScore(player)
{
  let rankScore;
  
  if (player.gamesData === null)
  {
    return 0;
  }
  
  if (player.gamesData.tier === "IRON")
    rankScore = 0;
  else if (player.gamesData.tier === "BRONZE")
    rankScore = 400;
  else if (player.gamesData.tier === "SILVER")
    rankScore = 800;
  else if (player.gamesData.tier === "GOLD")
    rankScore = 1200;
  else if (player.gamesData.tier === "PLATINUM")
    rankScore = 1600;
  else if (player.gamesData.tier === "EMERALD")
    rankScore = 2000;
  else if (player.gamesData.tier === "DIAMOND")
    rankScore = 2400;
  else 
    rankScore = 2800;
    
  return rankScore + player.gamesData.LeaguePoints;
}

function getPlayerTierIcon(player)
{
  let icon;
  
  if (player.gamesData === null)
    return './assets/UNRANKED.webp';
  
  if (player.gamesData.tier === "IRON")
    icon = './assets/IRON.webp';
  else if (player.gamesData.tier === "BRONZE")
    icon = './assets/BRONZE.webp';
  else if (player.gamesData.tier === "SILVER")
    icon = './assets/SILVER.webp';
  else if (player.gamesData.tier === "GOLD")
    icon = './assets/GOLD.webp';
  else if (player.gamesData.tier === "PLATINUM")
    icon = './assets/PLATINUM.webp';
  else if (player.gamesData.tier === "EMERALD")
    icon = './assets/EMERALD.webp';
  else if (player.gamesData.tier === "DIAMOND")
    icon = './assets/DIAMOND.webp';
  else if (player.gamesData.tier === 'MASTER')
    icon = './assets/MASTER.webp';
  else  if (player.gamesData.tier === 'GRANDMASTER')
    icon = './assets/GRANDMASTER.webp';
  else if (player.gamesData.tier === 'CHALLENGER')
    icon = './assets/CHALLENGER.webp'
  else 
    icon = './assets/UNRANKED.webp';
    
  return icon;
}

function getPlayerRole(player)
{
  let role;
  
  if (player.role === "TOP")
    role = './assets/TOP.webp';
  else if (player.role === "JNG")
    role = './assets/JUNGLE.webp';
  else if (player.role === "MID")
    role = './assets/MID.webp';
  else if (player.role === "ADC")
    role = './assets/ADC.webp';
  else 
    role = './assets/SUP.webp';
    
  return role;
}

function getPlayerWinrate(player)
{
    if(player.gamesData === null)
    {
        return 0;
    }

  let winrate = (player.gamesData.wins / ( player.gamesData.wins + player.gamesData.losses ) * 100).toFixed(1);
  return winrate;
}

function getPlayerLive(player)
{
  if (player.isLive)
    return './assets/TWITCH.png';
  else
    return './assets/TWITCHDOWN.png';
}

export function getTransformPlayersData(playersData)
{
    let fWins;
    let fLosses;
    
    let transformData = playersData.map(function(player) 
    {
    if (player.gamesData === null)
    {
        fWins = 0;
        fLosses = 0;
    } else {
        fWins = player.gamesData.wins;
        fLosses = player.gamesData.losses;
    }
    return {
      rank : 0,
      pseudo : player.pseudo,
      structure : player.structure,
      tier : getPlayerTier(player),
      tierIcon : getPlayerTierIcon(player),
      summonerName : getPlayerSummonerName(player),
      role : getPlayerRole(player),
      winrate : getPlayerWinrate(player),
      wins : fWins,
      losses : fLosses,
      hidenScore : getPlayerHidenScore(player),
      twitchLink : player.twitchLink,
      isLive : getPlayerLive(player),
      xLink : player.xLink
    }
  })
  
  transformData.sort(function (a, b) {
    return b.hidenScore - a.hidenScore;
  })
  
  transformData.map((data, index) =>
  {
    data.rank = index + 1;
    return null;
  })
  
  return transformData;
}